import VXPayEnvironment from "./VXPayEnvironment";

export default class VXPayConfig {

	/**
	 * Set default config structure
	 */
	constructor() {
		this._env     = VXPayEnvironment.DEVELOPMENT;
		this._logging = false;
		this._urls    = {
			abg: '',
			privacy: ''
		};
	}

	/**
	 * @param {String} url
	 * @return {boolean}
	 * @private
	 */
	static _isValidUrl(url) {
		try {
			new URL(url);
			return true;
		} catch (_) {
			return false;
		}
	}

	/**
	 * @return {String}
	 */
	get abgUrl() {
		return this._urls.abg;
	}

	/**
	 * @param {String} url
	 */
	set abgUrl(url) {
		if (!VXPayConfig._isValidUrl(url)) {
			throw new Error('Invalid URL provided: '  + url);
		}

		this._urls.abg = url;
	}

	/**
	 * @return {String}
	 */
	get privacyUrl() {
		return this._urls.privacy;
	}

	/**
	 * @param {String} url
	 */
	set privacyUrl(url) {
		if (!VXPayConfig._isValidUrl(url)) {
			throw new Error('Invalid URL provided: '  + url);
		}

		this._urls.privacy = url;
	}

	/**
	 * @return {String}
	 */
	get env() {
		return this._env;
	}

	/**
	 * @param {String} value
	 */
	set env(value) {
		this._env = value;
	}

	/**
	 * @return {Boolean}
	 */
	get logging() {
		return this._logging;
	}

	/**
	 * @param {Boolean} value
	 */
	set logging(value) {
		this._logging = value;
	}
}



